import React from 'react';
import {inject, observer} from 'mobx-react';
import {Card, Form, Row, Col, Button, Calendar, Dropdown, Spin, message, Modal} from 'antd';
import EditableTable from '@/components/labor/EditableTable';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as utils from "@/utils/utils";
import moment from 'moment';
import * as  mobx from 'mobx';
const { confirm } = Modal;
@Form.create()
@observer
class Delivery extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			hidden: true,  // 
			dataSource: [],   // 
			currentTree: {}, // 
			pid: '',     // id
			projectId: "", // id
			defaultTree: {}, // 
			calendarVis: false,
			isLoading: false,
		};
	}

	componentWillReceiveProps(nextProps) {
		const {treeData, disabled} = nextProps;
		if (typeof treeData == 'object' && Object.keys(treeData).length !== 0 && treeData !== this.props.treeData) {
			if (treeData.children === null) { // 
				this.setState({
					dataSource: []
				});
			} else {  // 
				const dataSource = treeData.children.filter((item) => item.treeType == "taskc")[0].children;
				this.setState({
					dataSource,
					currentTree: Object.assign({}, treeData),   // 
					pid: treeData.children.filter((item) => item.treeType == "taskc")[0].id,
					projectId: treeData.children.filter((item) => item.treeType == "taskc")[0].projectId,
					//hidden: disabled,
				}, () => {
					// 
					let attc = treeData.children.filter((item) => item.treeType == "attc")[0]
					if (Object.keys(this.state.defaultTree).length > 0) {
						// tree
						let defaultTree = JSON.parse(JSON.stringify(this.state.defaultTree));
						this.setState({
							defaultTree: utils.updateTreeData(defaultTree, "update", attc)
						});
					}
				})
			}
		}
	}

	componentDidMount() {
		const {treeData, disabled} = this.props;
		if (typeof treeData == 'object' && Object.keys(treeData).length !== 0) {
			if (treeData.children === null) { // 
				this.setState({
					dataSource: []
				});
			} else {  // 
				const dataSource = treeData.children.filter((item) => item.treeType == "taskc")[0].children;
				this.setState({
					dataSource,
					currentTree: Object.assign({}, treeData),
					pid: treeData.children.filter((item) => item.treeType == "taskc")[0].id,
					projectId: treeData.children.filter((item) => item.treeType == "taskc")[0].projectId,
					//hidden: disabled
				})
			}
		}
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {currentTree} = _this.state;
		_this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				_this.props.saveNodes(currentTree);
				this.setState({
					hidden: !this.state.hidden
				});
			}
		});
	}

	//  
	saveData2 = (taskdata, key) => {
		const {currentTree} = this.state;
		let newData = [];
		taskdata.forEach(item => {
			let {key, children, ...obj} = item;
			newData.push(obj);
		});
		let currentTaskC = currentTree.children.filter(item => item.treeType === 'taskc')[0];
		currentTaskC = Object.assign(currentTaskC, {"children": taskdata})
		let stepChildren = currentTree.children;
		const taskcIndex = stepChildren.findIndex(item => {
			return item.treeType === 'taskc'
		})
		stepChildren[taskcIndex] = currentTaskC;
		this.setState({currentTree: Object.assign(currentTree, {"children": stepChildren})});
	}

	// 
	onChangeVis = () => {
		this.setState({
			hidden: !this.state.hidden,
		});
		this.props.updateCount('payCount', Number(this.props.count) + 1)
	}

	// 
	onCanlc = () => {
		this.props.form.resetFields();
		this.setState({
			hidden: !this.state.hidden
		});
	}

	// 
	onPanelChange = () => {
		this.setState({
			isLoading: true
		},()=>{
			setTimeout(()=>{
                 this.setState({
	                 isLoading: false
                 }, ()=>{
	                 // 
	                 this.store.getProjectByWsProjectId().then((res)=>{
                         const {title, projectId}=res;
                         if (projectId!==null&&projectId!=="") {
	                         let url = 'https://www.bocspace.cn/eeop/#/xmgl?projectId='+`${projectId}`+'&name='+`${title}`;
	                         window.open(url, '_bank');
                         } else {
                           this.showConfirm();
                           return false;
                         }
	                 });
                 })
			}, 1000);
		});
	}

	onVisibleChange = () => {
		this.setState({
			calendarVis: !this.state.calendarVis
		});
	}

	disabledDate = (current) => {
		return current && current > moment().endOf('day');  //
	}

	// 
	updatePerson = (userIds)=>{
		const {currentTree} = this.state;
		// 
		const currentData = Object.assign({}, currentTree, {masters:userIds});
		// tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
	}

	dateCellRender = (value) => {
		const {reportDays}=this.store;
		const val = moment(value).format("YYYY-MM-DD");
		const isShow = mobx.toJS(reportDays).includes(val);
		return (
			<img
				className={isShow?"":"hidden"}
				style={{position: 'relative', 'top': '-5px'}}
				with="16px"
				height="16px"
				src="./images/zhmy_succes.png" alt=""/>
		);
	}

	showConfirm = () => {
		confirm({
			className: "tc modal_tc",
			//iconType: 'exclamation-circle',
			icon: null,
			title: '',
			content: '',
			onOk() {
				let url = 'https://www.bocspace.cn/eeop/#/construction';
				window.open(url, '_bank');
			},
			onCancel() {

			},
			okText: '',
			cancelText: '',
		});
	}

	render() {
		let _this = this;
		const {getFieldDecorator} = _this.props.form;
		const {count, isShow} = this.props;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const {state, projectId} = this.store;
		const {isLoading, calendarVis, hidden, currentTree, pid} = _this.state;
		let currentDataSource = currentTree.children != null && currentTree.children[0].children != null ? currentTree.children[0].children : [];

		return (
			<Card bordered style={{width: '100%'}} className="mt20">
				<Spin tip={"..."} spinning={isLoading} delay={1000}>
				<h3 id={currentTree.id} className="kanban_title mb20"></h3></Spin>
				<span className="worktile_progress_add">
                     {hidden ?
	                     <Button disabled={isShow} className={state == 2 ? "hidden" : ""} type="link"
	                             onClick={this.onChangeVis}>{count == 1 ? "" : ""}</Button> :
	                     <div>
		                     <span onClick={this.onCanlc} className="mr10"></span>
		                     <span onClick={this.handleSubmit}></span>
	                     </div>}
                </span>
				<Form className={count == 1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('masters', {
									initialValue: currentTree&&currentTree.masters==null?[]:currentTree.masters.map(item=>item.userId),
									rules: [{required: false, message: ''}],
								})(
									<PersonAddIcon
										data={currentTree&&currentTree.masters==null?[]:currentTree.masters}
										updatePerson={this.updatePerson}
										disabled={hidden}
									/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<img
								width='32px'
								height="32px"
								style={{verticalAlign: 'middle'}}
								src='./images/delivery.png'/>
							<span></span>
							<Dropdown
								visible={calendarVis}
								onVisibleChange={this.onVisibleChange}
								overlay={<div style={{
									width: 300,
									background: '#FFF',
									border: '1px solid #d9d9d9',
									borderRadius: 4
								}}>
									<Calendar
										disabledDate={this.disabledDate}
										dateCellRender={this.dateCellRender}
										fullscreen={false}
										onSelect={this.onPanelChange}/>
								</div>}>
								<a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
									
								</a>
							</Dropdown>
						</Col>
					</Row>
				</Form>
				{/**/}
				{count == 1 ? null : <EditableTable
					dataSource={currentDataSource}
					saveData={this.saveData2}
					projectId={projectId}
					pid={pid}
					disabled={hidden}
					store={this.store}
					saveAttachment={this.props.saveAttachment}
					delNode={this.props.delNode}
					ossData={this.props.ossData}
				/>}
			</Card>
		);
	}
}


export default Delivery;



