/*
* 
* */
import React from "react";
import { Upload, Icon, message } from 'antd';
import oss from 'ali-oss';
import moment from 'moment';

function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

const client = (self) => {
    const {token} = self.state
    // ali-oss v6.x
    return new oss({
      accessKeyId: token.access_key_id,
      accessKeySecret: token.access_key_secret,
      stsToken: token.stsToken,
      region: token.region, //
      bucket: token.bucket,//
      endpoint: token.OSS_ENDPOINT
    });
};

const uploadPath = (path, file) => {
    // 
    return `${moment().format('YYYYMMDD')}/${file.uid}/${file.name}`
}

const UploadToOss = (self, path, file) => {
    const url = uploadPath(path, file)
    return new Promise((resolve, reject) => {
      client(self).multipartUpload(url, file).then(data => {
        resolve(data); //?
      }).catch(error => {
        reject(error)
      })
    })
}

class PayUpload extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileList: this.props.fileList,
            loading: false,
            token: {
                access_key_id: '',//'STS.NUKFXpmcLY7gZeyDE7jMW6M5v', // osskey_id
                access_key_secret: '',//'5S1Ky3Beef6ZtXxTRPBMPvqV9ouTWkvMTmGhffJqeLK8', // osssecret
                stsToken: '',//'\u003d\u003d',
                region: 'oss-cn-beijing', // ossregion
                bucket: 'xinlj', // ossbucket
                OSS_ENDPOINT: 'oss-cn-beijing.aliyuncs.com',  // oss
            },
            host: 'https://xinlj.oss-cn-beijing.aliyuncs.com/', // host
        };
    }

	componentWillReceiveProps(nextProps) {
	    if (JSON.stringify(nextProps.fileList)!=JSON.stringify(this.props.fileList)) {
           this.setState({
               fileList: nextProps.fileList
           });
        }
        if (JSON.stringify(nextProps.ossData)!=JSON.stringify(this.props.ossData)) {
            let token = JSON.parse(JSON.stringify(this.state.token));
	        this.setState({
                token: Object.assign(token, {
                    access_key_id: nextProps.ossData.accessKeyId, // osskey_id
                    access_key_secret: nextProps.ossData.accessKeySecret, // osssecret
                    stsToken: nextProps.ossData.securityToken
                })
            });
        }
    }

    componentDidMount() {
        if (this.props.fileList) {
            this.setState({
                fileList: this.props.fileList
            });
        }
       if (this.props.ossData) {
           let token = JSON.parse(JSON.stringify(this.state.token));
           this.setState({
               token: Object.assign(token, {
                   access_key_id: this.props.ossData.accessKeyId, // osskey_id
                   access_key_secret: this.props.ossData.accessKeySecret, // osssecret
                   stsToken: this.props.ossData.securityToken
               })
           });
       }

    }

    handleChange = (info) => {
        const status = info.file.status;

        // 
        let fileList = [...info.fileList];
        let oldFileList = JSON.parse(JSON.stringify(fileList)).filter((file)=>{return file.status&&file.status === 'done';});  // 
        let newFileList = JSON.parse(JSON.stringify(fileList));
        newFileList = newFileList.filter((file) => {
            if (file.status&&file.status === 'done') {
                return false;
            }
            return true;
        });
        newFileList = newFileList.map(file => {
            if (file.originFileObj) {
                const url = this.state.host+uploadPath('path', file);
                return  Object.assign({}, {
                    uid:file.uid,
                    name:file.name,
                    status:"done",
                    url:url,
                    fileId: null,
                    fileName: file.name,
                    filePath: url,
                    id: ""
                })
            }
            return file;
        });
            if (status==undefined) {
         // 
         //let array = JSON.parse(JSON.stringify([...oldFileList, ...newFileList]))
         let array = JSON.parse(JSON.stringify([ ...newFileList]))
         let arr = [];
         array.length && array.map((item, index) => {
        	 arr.push({
        		 fileId: item.fileId,
        		 fileName: item.fileName,
        		 filePath: item.filePath,
        		 pid: this.props.pid,
        		 projectId: this.props.projectId,
        		 id: item.id
        	 })
         });
         this.props.saveAttachment(arr);
        }
        else if (status === "removed") {   // 
        	 let id = info.file.id;
        	 if (id!==""||id!==null) { // 
        		 this.props.delNode(id);
        	 }
        }

    this.setState({
            fileList: [...oldFileList, ...newFileList]
        });
    }

    beforeUpload = (file) => {
        const isJPG = file.type === 'image/jpeg';
        if (!true) {
          message.error('You can only upload JPG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2000;
        if (!isLt2M) {
          message.error('Image must smaller than 2MB!');
        }
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = () => {
            // ossupload
            UploadToOss(this, 'oss', file)
                .then(data => {return data;});
        }
        return false; // 
    }
    render() {
        const {fileList} = this.state;
        const props = {
            name: 'file',
            multiple: this.props.multiple,
            onChange: this.handleChange,
            beforeUpload: this.beforeUpload,
            listType: 'text',
            className: 'upload-list-inline',
            disabled: this.props.disabled,
        };
        return (
            <Upload {...props} fileList={fileList}>
                <Icon style={{fontSize: '25px', color: '#53B3E5'}} type="cloud-upload"/>
            </Upload>
        );
	}
}

export default PayUpload;



