import React from 'react';
import {inject, observer} from 'mobx-react';
import {Breadcrumb, message} from 'antd';
import url from 'url';
import * as  mobx from 'mobx';
import ProgressList from '@/components/labor/ProgressList';
import Ordercenter from '@/components/labor/Ordercenter';
import Delivery from '@/components/labor/Delivery';
import WortileCommon from '@/components/labor/WortileCommon';
import * as utils from "@/utils/utils";
import emitter from "@/utils/events";

@inject('LarorStore')
@observer
class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.LarorStore;
		this.state = {
			count: 1, // 1 2 
			payCount: 1, // 1 2 
		};
		mobx.autorun(() => {
			this.store.commonData = this.store.getcommonData;
			this.store.payObj = this.store.getproDesign;
		});
	}

	componentWillMount() {

	}

	componentDidMount() {
		let search = this.props.location.search;
		const {projectid, temId, temName, id, pid, stageid, isEdit} = url.parse(search, true).query;
		this.store.temId = temId;
		this.store.temName = temName;
		// sts
		this.store.getSts();
		if (projectid != null && projectid != undefined) { // 
			this.store.projectId = projectid;
			this.store.getTree(projectid).then((mes) => {
				this.store.treeData = Object.assign({}, mes); // 
				// 
				let itemObj = Object.assign({}, mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName == "")[0]);
				// 
				this.store.payObj = Object.assign({}, mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName == "")[0]);
				// 
				this.store.commonData = Object.assign([], mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName !== "" && item.stageName !== ""));
				// 
				let commonData = JSON.parse(JSON.stringify(this.store.commonData));
				let obj = {};
				let countObj = {};
				commonData.map((v) => {
					obj[v.id] = true;
					countObj[v.id] = 2;
				});
				this.store.comStates = Object.assign({}, obj);
				this.store.comCount = Object.assign({}, countObj);
				this.store.itemObj = Object.assign({}, itemObj);
				this.store.projectName = itemObj.children.filter((item) => {return item.treeType == "projectinfo"})[0].name;
				// 
				this.store.getStageTree();
				this.store.getReportDaysList();
				this.setState({
					count: 2,
					payCount: 2
				});
				if(pid!=undefined&&id!=undefined&&pid!=null){
					emitter.emit("handletaskid",  {"pid":pid, "id":id, "isEdit":isEdit});    // 
				}else if (id!=undefined) {  // 
					this.scrollToAnchor(id);
				}else if(stageid!==undefined){
					//this.scrollToAnchor(`items${parseInt(stageid)-1}`);
				}
			});
		} else {  // 
			this.store.projectId = "";
			this.store.projectName = "";
			// 
			this.store.getStageTree();
			// 
			let commonData = JSON.parse(JSON.stringify(this.store.commonData));
			let obj = {};
			let countObj = {};
			commonData.map((v) => {
				obj[v.id] = true;
				countObj[v.id] = 1;
			});
			this.store.comStates = Object.assign({}, obj);
			this.store.comCount = Object.assign({}, countObj);
			this.store.itemObj = Object.assign({},{
				"id": "",
				"pid": "",
				"projectId": "",
				"treeType": "stage",
				"masters": null,
				"children": [
					{
						"id": "",
						"pid": "",
						"projectId": "",
						"treeType": "projectinfo",
						"masters": null,
						"children": [
							{
								"id": "",
								"pid": "",
								"projectId": "",
								"treeType": "orderinfo",
								"masters": null,
								"children": null,
								"customerPhone": null,
								"productNumber": null,
								"priceProduct": 0,
								"priceDiscount": "",
								"priceDeal": 0,
								"orderRemark": null,
								"json": null
							}
						],
						"code": null,
						"name": null,
						"customer": null,
						"productType": ["5"],
						"address": null,
						"description": null,
						"milepost": null,
						"builtArea": null,
						"totalCost": null,
						"builtType": null,
						"beginTime": 0,
						"endTime": 0,
						"constructionUnit": null,
						"constructionQualification": null,
						"partyaUnit": null,
						"unitQualification": null,
						"declarationAwards": null,
						"awardGrade": null,
						"buySoftware": null,
						"usageSituation": null,
						"background": null,
						"temporaryDecision": null,
						"temporaryManagerDesire": null,
						"temporaryRequirement": null,
						"temporaryBudgetStandard": null,
						"temporaryTotalAcceptability": null,
						"temporaryTotalRange": null,
						"temporaryBudget": null,
						"temporaryPriceDesire": null,
						"temporaryEstimatePrice": null,
						"temporaryManagersNumber": null,
						"temporaryWorkersNumber": null,
						"temporaryBeginTime": 0,
						"temporaryEndTime": 0,
						"temporaryBudgetEvaluation": null,
						"template": 2,
						"currentTaskState": 0,
						"currentProgress": 0,
						"state": 0,
						"stageId": null,
						"closeReason": "",
						"projectType": 2
					},
					{
						"id": "",
						"pid": "",
						"projectId": "",
						"treeType": "attc",
						"masters": null,
						"children": null,
						"name": null,
						"type": 0
					}
				],
				"stageName": ""
			})
		}
	}

	onRef = (ref) => {
		this.child = ref;
	}

	// 
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
			try {
				let anchorElement = document.getElementById(anchorName);
				if (anchorElement) {
					anchorElement.scrollIntoView({block: 'center'});
				}
			} catch (err) {
			}
		}
	}

	// 
	saveProject = (params) => {
		this.store.saveProject(params)
		.then((res) => {
			if (res.id !== null) {
				message.success("")
				this.child.onChangeVis();
				this.store.projectId = res.id;
				this.store.closeReason = res.closeReason;
				this.store.projectName = res.name;
				this.store.state = res.state;
				// 
				this.store.getTree(res.id).then((mes) => {
					this.store.treeData = Object.assign({}, mes); // 
					//  
					this.store.itemObj = Object.assign({}, mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName == "")[0]);
					//
					this.store.commonData = Object.assign([], mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName !== "" && item.stageName !== ""));
					// 
					let commonData = JSON.parse(JSON.stringify(this.store.commonData));
					let obj = {};
					let countObj = {};
					commonData.map((v) => {
						obj[v.id] = true;
						countObj[v.id] = 1;
					});
					this.store.comStates = Object.assign({}, obj);
					this.store.comCount = Object.assign({}, countObj);
					// 
					this.store.getStageTree();
					this.store.getReportDaysList();
				});
			} else {
				message.error("")
			}
		});
	}

	// 
	saveAttachment = (params) => {
		this.store.saveAttachments(params)
		.then((res) => {
			if (res) {
				message.success("")
				// tree
				let treeData = JSON.parse(JSON.stringify(this.store.treeData));
				this.store.treeData = utils.updateTreeData(treeData, "updateChildren", res);
			} else {
				message.error("")
			}
		});
	}

	// 
	delNode = (param) => {
		this.store.delNode(param)
		.then((res) => {
			if (res) {
				message.success("")
			} else {
				message.error("")
			}
		});
	}

	// 
	updateCount = (type, count) => {
		this.setState({
			[type]: count
		});
	}

	// 
	saveNodes = (params) => {
		this.store.saveNodes(params).then((res) => {
			if (res) {
				message.success("")
				// tree
				let treeData = JSON.parse(JSON.stringify(this.store.treeData));
				this.store.treeData = utils.updateTreeData(treeData, "update", res);
				// 
				this.store.getStageTree();
				//
				//this.store.getMyTaskCount();
			} else {
				message.error("")
			}
		});
	}

	// 
	updateTree = () => {
		const projectId = this.store.projectId;
		this.store.getTree(projectId).then((mes) => {
			this.store.treeData = Object.assign({}, mes); // 
			//  
			let itemObj = Object.assign({}, mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName == "")[0]);
			//
			this.store.commonData = Object.assign([], mes && mes.children.filter((item) => item.treeType == "stage" && item.stageName !== "" && item.stageName !== ""));
			// 
			let commonData = JSON.parse(JSON.stringify(this.store.commonData));
			let obj = {};
			let countObj = {};
			commonData.map((v) => {
				obj[v.id] = true;
				countObj[v.id] = 2;
			});
			this.store.itemObj = Object.assign({}, itemObj);
			this.store.comStates = Object.assign({}, obj);
			this.store.comCount = Object.assign({}, countObj);
			let projectinfo = itemObj.children.filter((item) => {return item.treeType == "projectinfo"})[0];
			this.store.projectName = projectinfo["name"];
			this.store.state = projectinfo["state"];
			this.store.closeReason = projectinfo["closeReason"];
			// 
			this.store.getStageTree();
		});
	}

	render() {
		const {projectId, projectName, listData, itemObj, ossData, payObj, commonData, comStates, comCount} = this.store;
		const {count, payCount} = this.state;
		let isShow = projectId == null || projectId == "" ? true : false;
		return (
			<div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/bpm/progresslist"}></Breadcrumb.Item>
						<Breadcrumb.Item>{projectName || ""}</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				{/**/}
				<ProgressList
					listData={mobx.toJS(listData)}
					store={this.store}
					isShow={isShow}
					scrollToAnchor={this.scrollToAnchor}
					updateTree={this.updateTree}
				/>
				{/**/}
				<Ordercenter
					store={this.store}
					itemObj={mobx.toJS(itemObj)}
					saveProject={this.saveProject}
					onRef={this.onRef}
					saveAttachment={this.saveAttachment}
					delNode={this.delNode}
					ossData={mobx.toJS(ossData)}
					disabled={true}
					count={count}
					updateCount={this.updateCount}
				/>
				{/**/}
				{mobx.toJS(commonData).length > 0 && mobx.toJS(commonData).filter((item) => item.treeType == "stage" && item.stageName !== "").map((item, index) => {
					return (<WortileCommon
						id={item.id}
						rowIndex={"items" + (index + 2)}
						store={this.store}
						treeData={item}
						saveNodes={this.saveNodes}
						saveAttachment={this.saveAttachment}
						delNode={this.delNode}
						disabled={mobx.toJS(comStates)[item.id]}
						oss={mobx.toJS(ossData)}
						isShow={isShow}
						count={mobx.toJS(comCount)[item.id]}
					/>)
				})}
				{/**/}
				<Delivery
					store={this.store}
					treeData={mobx.toJS(payObj)}
					count={payCount}
					updateCount={this.updateCount}
					saveAttachment={this.saveAttachment}
					delNode={this.delNode}
					ossData={mobx.toJS(ossData)}
					disabled={true}
					isShow={isShow}
					saveNodes={this.saveNodes}
				/>
				{/**/}
				{mobx.toJS(commonData).length > 0 && mobx.toJS(commonData).filter((item) => item.treeType == "stage" && item.stageName == "").map((item, index) => {
					return (<WortileCommon
						id={item.id}
						rowIndex={"items" + (index + 2)}
						store={this.store}
						treeData={item}
						saveNodes={this.saveNodes}
						saveAttachment={this.saveAttachment}
						delNode={this.delNode}
						disabled={mobx.toJS(comStates)[item.id]}
						oss={mobx.toJS(ossData)}
						isShow={isShow}
						count={mobx.toJS(comCount)[item.id]}
					/>)
				})}
			</div>
		);
	}
}


export default Index;




