/*
* Table
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	Table,
	Input,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	Select,
	message,
	InputNumber,
	Dropdown,
	Menu,
	Popover
} from 'antd';
import TaskSplite from '@/components/worktile/TaskSplite';
import moment from 'moment';
import emitter from "@/utils/events";
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';
import PersonAdd2 from "@/components/worktile/PersonAdd";
import PayUpload from "@/components/labor/PayUpload";

let runonlyonce = false;
const {Option} = Select;
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
const widthDESIGN = {2: '13%', 4: '10%'};  // 


/**/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			//this.toggleEdit();
			delete values["beginTime"];
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	};
	//
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	handleSelChange = (val, e) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (e.length) {
			userIds = e.map((item) => {
				return {userId: item.props.value, userName: item.props.children}
			})
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		handleSave({...record, ...data});
	}

	//
	updatePerson = (users) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (users.length) {
			userIds = users
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		handleSave({...record, ...data});
	}
	onBlur = () => {
		//this.toggleEdit();
	}
	//
	disabledStartDate = startValue => {
		const {record} = this.props;
		if (!startValue || !record.endTime) {
			return false
		}
		return startValue.valueOf() > record.endTime.valueOf()
	}
	//
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() <= record.beginTime.valueOf()
	}
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, personData, handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'name') {  // 
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}`,
						}],
						initialValue: record[dataIndex],
					})(
						<div className="w">
							<Input
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								//onChange={this.save}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{
						float: 'right',
						width: 'calc(100% - 35px)',
						minHeight: '31px'
					}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'masters') {  // 
			return editing ? (<Form.Item style={{margin: 0}} className="w">
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex].length == 0 ? [] : record[dataIndex].length > 0 && record[dataIndex].map((item) => {
						return item.userId
					}),
				})(
					<div className="w" ref={node => (this.input = node)}>
						<PersonAdd2
							data={record[dataIndex] == null || record[dataIndex].length == 0 ? [] : record[dataIndex].length > 0 && record[dataIndex]}
							updatePerson={this.updatePerson}
							disabled={!editing}
						/>
					</div>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}>
					{children}
				</div>
			);
		} else if (dataIndex == 'beginTime') { // 
			// 
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						style={{minWidth: '120px'}}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate}
						//
						disabledDate={this.disabledStartDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'endTime') { // 
			// 
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						style={{minWidth: '120px'}}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate2}
						//
						disabledDate={this.disabledEndDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'rate') {  // 
			let subTasks = record.children != null && record.children.filter((item) => {
				return item.treeType === 'subtask'
			})
			const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
			const isEdit = subNumber > 0 ? true : false;
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							disabled={isEdit}
							min={0}
							max={100}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		} else if (dataIndex == 'remark') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder=""
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else {  // 
			const subNumber = record.children == null || record.children.length == 0 ? 0 : record.children.length
			const isEdit = subNumber > 0 ? true : false;
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							disabled={isEdit}
							min={0}
							max={100}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			personData,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* table
* */
@observer
class EditableTable extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 
			count: 0,        // tablekey
			primaryData: {
				"id": "",
				"pid": "",
				"projectId": "",
				"treeType": "task",
				"masters": [],
				"children": [
					{
						"id": "",
						"pid": "",
						"projectId": "",
						"treeType": "attc",
						"masters": null,
						"children": null,
						"name": "",
						"type": 0
					}],
				"name": "",
				"rate": 0,
				"nodeCount": null,
				"beginTime": null,
				"endTime": null,
				"orders": 0,
				"remark": ""
			}, // 
			expandedKeys: [], // 
			keys: [], // 
			subtasks: [],
			activeObj: {}, // 
			activeSub: [], // 
			subTaskDis: false, // 
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {key: index + 1})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"id": "",
					"pid": nextProps.pid,
					"projectId": nextProps.projectId,
					"treeType": "task",
					"masters": [],
					"children": [
						{
							"id": "",
							"pid": "",
							"projectId": this.props.projectId,
							"treeType": "attc",
							"masters": null,
							"children": null,
							"name": "",
							"type": 0
						}],
					"name": "",
					"rate": 0,
					"nodeCount": null,
					"beginTime": null,
					"endTime": null,
					"orders": 0,
					"remark": ""
				}) // 
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index + 1})
			}),
			count: this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"id": "",
				"pid": this.props.pid,
				"projectId": this.props.projectId,
				"treeType": "task",
				"masters": [],
				"children": [
					{
						"id": "",
						"pid": "",
						"projectId": this.props.projectId,
						"treeType": "attc",
						"masters": null,
						"children": null,
						"name": "",
						"type": 0
					}],
				"name": "",
				"rate": 0,
				"nodeCount": null,
				"beginTime": null,
				"endTime": null,
				"orders": 0,
				"remark": ""
			}), // 
			expandedKeys: [], // 
			keys: [], // 
			subtasks: [],
		});

		this.eventE3 = emitter.addListener('handletaskid', (obj)=>{
			this.getKeyByShowRow(obj.pid, obj.pid, obj.isEdit);
		});
	}

	componentWillUnMount(){
		emitter.removeListener(this.eventE3)
	}

	getKeyByShowRow = (key, id, isEdit) => {
		// 2  1
		if (isEdit == 2) {
			this.saveExpandedRows(key)
		} else {
			this.handleView(key);
		}
		this.scrollToAnchor(id);
	}

	// 
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
			try {
				let anchorElement = document.getElementById(anchorName);
				if (anchorElement) {
					anchorElement.scrollIntoView({block: 'center'});
				}
			} catch(err) {
				console.log("scrollToAnchorERR=>"+err);
			}
		}
	}

	// 
	handleDelete = (record) => {
		const {id, key} = record;
		if (id !== "" && id !== null && !id.startsWith("!")) { // 
			this.store.delNode(id).then((res) => {
				if (res) {
					message.success("");
					const dataSource = [...this.state.dataSource];
					this.setState({
						dataSource: dataSource.filter(item => item.key !== key)
					}, () => {
						this.props.saveData(this.state.dataSource);
						// tree
						let treeData = JSON.parse(JSON.stringify(this.store.treeData));
						this.store.treeData = utils.updateTreeData(treeData, "del", record);
					});
					// 
					this.store.getStageTree();
					//
					this.store.getMyTaskCount();
				} else {
					message.error("")
				}
			});
		} else { // 
			const dataSource = [...this.state.dataSource];
			this.setState({
				dataSource: dataSource.filter(item => item.key !== key)
			}, () => {
				this.props.saveData(this.state.dataSource, key);
				// tree
				let treeData = JSON.parse(JSON.stringify(this.store.treeData));
				this.store.treeData = utils.updateTreeData(treeData, "del", record);
			});
		}
	};

	// 
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		const {pid, projectId} = this.props;
		//const newData = Object.assign(primaryData, {key: count, id: "!" + uuidv4()});
		let uuid = "!" + uuidv4();
		const newData = Object.assign({}, {
			"id": uuid,
			"pid": pid,
			"projectId": projectId,
			"treeType": "task",
			"masters": [],
			"children": [
				{
					"id": "!" + uuidv4(),
					"pid": uuid,
					"projectId": projectId,
					"treeType": "attc",
					"masters": null,
					"children": null,
					"name": "",
					"type": 0
				}],
			"name": "",
			"rate": 0,
			"nodeCount": null,
			"beginTime": null,
			"endTime": null,
			"orders": 0,
			"remark": ""
		});
		//console.log(newData, '')
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			this.props.saveData(this.state.dataSource);
			// tree
			let treeData = JSON.parse(JSON.stringify(this.store.treeData));
			this.store.treeData = utils.updateTreeData(treeData, "add", newData);
		});
	};

	// 
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 
			this.props.saveData(this.state.dataSource);
			let newRow = JSON.parse(JSON.stringify({...row}));
			// tree
			let treeData = JSON.parse(JSON.stringify(this.store.treeData));
			this.store.treeData = utils.updateTreeData(treeData, "update", newRow);
		});
	};

	// 
	saveExpandedRows = (key) => {
		let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
		_this.setState({expandedKeys: keys}, () => {
			let obj = {[_this.props.id]: keys};
			let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
			_this.store.expandedKeys = Object.assign(expandedKeys, obj);
		});
	}

	// 
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 
		}, () => {
			this.saveExpandedRows(record);
		});
	}

	// 
	handleApart = (record) => {
		// if (key==null||key=="") {
		//    message.warn("");
		//    return false;
		// }
		// 
		this.setState({
			activeObj: Object.assign({}, record),
			activeSub: Object.assign([], record.children == null ? [] : record.children),
			subTaskDis: false, // 
		}, () => {
			this.saveExpandedRows(record.id);
		});
	}

	// 
	saveSubTaskRecord = (id) => {
		// 
		let length = this.state.subtasks.length
		if (length == 0) {
			this.store.delNodeByPid({pid: id})
			.then((res) => {
				if (res) {
					// 
					this.props.updateTree();
					// 
					this.store.getStageTree();
					//
					this.store.getMyTaskCount();
					message.success("")
					this.saveExpandedRows(id);
				} else {
					message.error("")
				}
			});
		} else {
			// 
			const subtasks = JSON.parse(JSON.stringify(this.state.subtasks));
			const isSub = subtasks.filter((item) => {
				return item.name == "" || item.masters == null || item.masters.length == 0
			})
			if (isSub.length > 0) {  // true
				message.warn("");
				return false;
			}
			// 
			if (id.startsWith("!")) {
				this.saveExpandedRows(id);
			} else {
				// 
				this.store.saveSubTasks(this.state.subtasks)
				.then((res) => {
					if (res && res.length > 0) {
						message.success("")
						this.subtask.clearState();
						// tree
						let treeData = JSON.parse(JSON.stringify(this.store.treeData));
						let record = utils.getRecord(treeData, id);
						//  attachments 
						let attachments = record && record.children != null && record.children.filter((item) => {
							return item.treeType === 'attc'
						})[0];
						if (attachments) {
							res.push(attachments);
						}
						this.store.treeData = utils.updateTreeData(treeData, "updateChildren", res);
						// 
						this.store.getStageTree();
						this.saveExpandedRows(id);
					} else {
						message.error("")
					}
				});
			}
		}
	}

	// 
	handleSaveSubTask = (subtasks) => {
		let newSubTasks = [];
		subtasks.forEach(item => {
			let {key, children, ...obj} = item;
			newSubTasks.push(obj)
		})
		this.setState({subtasks: newSubTasks})
	}

	// 
	onCalac = (key) => {
		this.saveExpandedRows(key);
		this.store.isEdit = false;
		// tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
		this.subtask.clearState();
		this.setState({
			activeObj: Object.assign({})
		});
	}

	// 
	onPackup = (record) => {
		let _this = this;
		const defaultSource = _this.state.activeSub.map(item => {
			return {
				"name": item.name,
				"masters": item.masters,
				"totalHours": item.totalHours,
				"alreadyHours": item.alreadyHours
			}
		});  // 
		const rearSource = _this.state.subtasks.map(item => {
			return {
				"name": item.name,
				"masters": item.masters,
				"totalHours": item.totalHours,
				"alreadyHours": item.alreadyHours
			}
		});  // 
		// console.log(JSON.stringify(defaultSource), JSON.stringify(rearSource), '===', JSON.stringify(defaultSource)==JSON.stringify(rearSource))
		// 
		if (JSON.stringify(defaultSource) == JSON.stringify(rearSource)) {
			// 
			_this.onCalac(record.id);
		} else {
			Modal.confirm({
				title: '',
				okText: '',
				cancelText: '',
				onOk() {
					_this.saveSubTaskRecord(record.id);
				},
				onCancel() {
					_this.onCalac(record.id);
				},
			});
		}
	}

	onTaskRef = (ref) => {
		this.subtask = ref
	}

	// 
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children == null ? [] : record.children.filter((item) => {
			return item.treeType === 'subtask'
		})
		const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
		if (disabled && subNumber > 0) {  // 
			this.handleView(record.id)
		}
	}

	handleChangeMenu = (type, item, rowId) => {
		const {id, fileType, filePath} = item;
		// 
		let payOutput = JSON.parse(JSON.stringify(this.store[type]));
		this.store[type] = Object.assign(payOutput, {
			[rowId]: JSON.stringify({
				fileName: item.fileName,
				filePath: filePath,
				id,
				fileType
			})
		});
	}

	// 
	handleLinkTo = (rowUrl, obj, rowId) => {
		const {id, fileType, filePath, fileName} = obj;
		var linkUrl;
		if (rowUrl != undefined && rowUrl != null) {
			const link = JSON.parse(rowUrl).filePath;
			const linkId = JSON.parse(rowUrl).id;
			const linkType = JSON.parse(rowUrl).fileType;
			const linkName = JSON.parse(rowUrl).fileName;
			if (link == "" || link == null) {
				const params = {
					id: linkId,
					type: linkType
				}
				this.store.getTaskattEditUrl(params)
				.then((res) => {
					if (res) {
						linkUrl = res;
						window.open(linkUrl, '_blank');
					}
				});
			} else {
				linkUrl = link;
				window.open(linkUrl, '_blank');
			}
            // 
			let payOutput = JSON.parse(JSON.stringify(this.store.payOutput));
			this.store.payOutput = Object.assign(payOutput, {
				[rowId]: JSON.stringify({
					fileName: linkName,
					filePath: linkUrl,
					id: linkId,
					fileType: linkType
				})
			});
		} else {   // rowUr
			if (filePath == "" || filePath == null) {
				const params = {
					id: id,
					type: fileType
				}
				this.store.getTaskattEditUrl(params)
				.then((res) => {
					if (res) {
						linkUrl = res;
						window.open(linkUrl, '_blank');
					}
				});
			} else {
				linkUrl = filePath;
				window.open(linkUrl, '_blank');
			}
			// 
			let payOutput = JSON.parse(JSON.stringify(this.store.payOutput));
			this.store.payOutput = Object.assign(payOutput, {
				[rowId]: JSON.stringify({
					fileName: fileName,
					filePath: linkUrl,
					id: id,
					fileType: fileType
				})
			});
		}
	}

	//
	handleLinkDown = (rowUrl, obj, rowId) => {
		const {filePath} = obj;
		if (rowUrl != undefined && rowUrl != null) {
			const link = JSON.parse(rowUrl).filePath;
			const linkArray = link.split(";");
			linkArray.forEach(item => {
				window.open(item, '_blank');
			});
		} else {   // rowUr
			const linkArray = filePath.split(";");
			linkArray.forEach(item => {
				window.open(item, '_blank');
			});
		}
	}

	// 
	download =(attc) => {
		if(attc.length>0) {
			const url = attc[0].filePath;
			window.open(url, '_blank');
		} else {
			return false;
		}
	}

	render() {
		const {subTaskDis, dataSource, expandedKeys} = this.state;
		const {temId} = this.store;
		let payOutput = mobx.toJS(this.store.payOutput);
		let temOutput = mobx.toJS(this.store.temOutput);
		const {disabled} = this.props;
		const {taskhaveattt} = this.props;
		const isChaijie = expandedKeys.length > 0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columnEle = [
			{
				title: '',
				dataIndex: 'key',
				align: "center",
				width: '70px',
				render: (text, record) => {
					return (
						<div
							style={{width: '70px'}}
							id={`/workdetail?projectid=${record.projectId}&id=${record.id}`}
							onClick={this.onRow.bind(this, record)}
							className="w pr">
							<Popconfirm title="?">
								<Icon
									className={!disabled && !isChaijie ? "" : "hidden"}
									style={{
										position: 'absolute',
										left: '0',
										color: '#F17E7E',
										fontSize: '18px',
										paddingRight: '5px'
									}}
									type="minus-circle"/>
							</Popconfirm>
							<span>{text}</span>
						</div>
					)
				}
			},
			{
				title: '',
				dataIndex: 'name',
				width: widthDESIGN[temId],
				align: "left",
				editable: !disabled && !isChaijie,
			},
			{
				title: '',
				dataIndex: 'masters',
				width: widthDESIGN[temId],
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						onClick={this.onRow.bind(this, record)}>{text == null ? "" : text.map(item => item.userName).join("")}</div>;
				},
			},
			{
				title: '',
				dataIndex: 'beginTime',
				//width: widthDESIGN[temId],
				width: 150,
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px', }}
						onClick={this.onRow.bind(this, record)}>{text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD")}</div>;
				},
			},
			{
				title: '',
				dataIndex: 'endTime',
				//width: widthDESIGN[temId],
				width: 150,
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						onClick={this.onRow.bind(this, record)}>{text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD")}</div>;
				},
			},
			{
				title: '',
				dataIndex: 'rate',
				width: widthDESIGN[temId],
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return <div  style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}
					            id={record.id}>{text}%</div>
				}
			},
			{
				title: '',
				dataIndex: 'rate',
				//width: widthDESIGN[temId],
				width: 150,
				align: "left",
				render: (text, record) => {
					const {children} = record;
					let attcArray = children != null && Array.isArray(children) && children.filter((item) => {
						return item.type == 0
					});  // 
					let onlineArray = children != null && Array.isArray(children) && children.filter((item) => {
						return item.type == 1
					});  // 
					if (attcArray.length > 0) {
						let attcObj = attcArray[0];  //
						const attachments = attcObj && attcObj.children && attcObj.children != null ? attcObj.children.map((vitem) => {
							return Object.assign({}, {
								uid: vitem.id,
								name: vitem.fileName,
								status: 'done',
								url: vitem.filePath,
								fileId: vitem.fileId,
								fileName: vitem.fileName,
								filePath: vitem.filePath,
								id: vitem.id
							})
						}) : [];
						let isNew = attcObj.id.includes("!");
						return (
							<div className="table_upload pay_upload"  style={{width: '100%', height: '46px', lineHeight: '46px'}}>
								<span
									className="ellsis"
									title={attcObj.name}
									onClick={this.download.bind(this, attachments)}
									style={{
										color: attachments.length > 0 ? '#1890ff' : '#333',
										cursor: attachments.length > 0 ? 'pointer' : 'default',
										padding: '0 5px',
										maxWidth: '80px',
										display:'inline-block',
										verticalAlign: 'middle',
									}}>
									{attcObj.name}
								</span>
								{disabled|| isChaijie||isNew?null:
								<PayUpload
									saveAttachment={this.props.saveAttachment}
									delNode={this.props.delNode}
									fileList={attachments}
									pid={attcObj ? attcObj.id : ""}
									projectId={attcObj ? attcObj.projectId : ""}
									disabled={disabled || isChaijie}
									ossData={this.props.ossData}
									multiple={false}
								/>}
							</div>)
					} else if (onlineArray.length > 0) {
						let onlineObj = onlineArray[0];  //
						const {children} = onlineObj;
						const rowUrl = payOutput[record.id];
						const fileUrl = rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).filePath : children[0].filePath;
						return (
							<div  style={{width: '100%', height: '46px', lineHeight: '46px'}}>
                            <Dropdown overlay={
	                            <Menu>
		                            {children.length > 0 && children.map((item, index) => {
			                            return (<Menu.Item
				                            onClick={this.handleChangeMenu.bind(this, 'payOutput', item, record.id)}
				                            key={"menu_" + index}>
				                            {item.fileName == null ? "--" : item.fileName}
			                            </Menu.Item>)
		                            })}
	                            </Menu>
                            }>
                                <span className={children.length == 1 ? "hidden" : "ant-dropdown-link"}
                                      onClick={e => e.preventDefault()}>
                                  <Icon type="caret-down"/>
                                </span>
                            </Dropdown>
								<span
									className="ellsis"
									onClick={disabled|| isChaijie?this.handleLinkTo.bind(this, rowUrl, children[0], record.id):null}
									title={children.length > 0 ? (rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).fileName : children[0].fileName) : null}
									style={{
										color: fileUrl == null || fileUrl == "" ? '#333' : '#1890ff',
										padding: '0 5px',
										maxWidth: '80px',
										display:'inline-block',
										verticalAlign: 'middle',
										cursor: 'pointer'
									}}>
									{children.length > 0 ? (rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).fileName : children[0].fileName) : null}
								</span>
								{disabled|| isChaijie?null:
         						<Icon type="edit" style={{color: "#1890ff"}}
					                  onClick={this.handleLinkTo.bind(this, rowUrl, children[0], record.id)}/>}
                        </div>);
					} else {
						return null;
					}
				},
			},
			{
				title: '',
				dataIndex: 'template',
				className: temId == 2 ? "hidden" : "",
				//width: widthDESIGN[temId],
				width: 150,
				align: "left",
				render: (text, record) => {
					if (temId == 2) { //  - 
						return null;
					} else {
						const {children, treeType} = record;
						if (treeType == "task") {
							let temArray = children != null && children != undefined && Array.isArray(children) && children.filter((item) => {
								return item.type == 2
							});  // 
							if (temArray.length == 0) {
								return (<div className="tc" style={{width: '70px'}}> - - </div>)
							} else {
								let temObj = temArray[0];  // 
								const temChildren = temObj.children == null ? [] : temObj.children;
								//var fileUrl;
								var rowUrl;
								if (temOutput !== undefined) {
									rowUrl = temOutput[record.id];
									//fileUrl = rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).filePath : children[0].filePath;
								}
								return (
									<div style={{width: '100%', height: '46px', lineHeight: '46px'}}>
                            <Dropdown overlay={
	                            <Menu>
		                            {temChildren.length > 0 && temChildren.map((item, index) => {
			                            return (<Menu.Item
				                            onClick={this.handleChangeMenu.bind(this, 'temOutput', item, record.id)}
				                            key={"menu_" + index}>
				                            {item.fileName == null ? "--" : item.fileName}
			                            </Menu.Item>)
		                            })}
	                            </Menu>
                            }>
                                <span className={temChildren.length == 1 ? "hidden" : "ant-dropdown-link"}
                                      onClick={e => e.preventDefault()}>
                                  <Icon type="caret-down"/>
                                </span>
                            </Dropdown>
								<span
									className="ellsis"
									title={children.length > 0 ? (rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).fileName : temChildren[0].fileName) : null}
									style={{
										//color: fileUrl == undefined || fileUrl == null || fileUrl == "" ? '#333' : '#1890ff',
										padding: '0 5px',
										maxWidth: '80px',
										display:'inline-block',
										verticalAlign: 'middle'
									}}>
									{children.length > 0 ? (rowUrl !== undefined && rowUrl !== null ? JSON.parse(rowUrl).fileName : temChildren[0].fileName) : null}
								</span>
								<Icon type="vertical-align-bottom" style={{color: "#1890ff", fontSize: '20px'}}
								      onClick={this.handleLinkDown.bind(this, rowUrl, temChildren[0], record.id)}/>
                        </div>)
							}
						}
					}
				}
			},
			{
				title: '',
				dataIndex: 'remark',
				width: widthDESIGN[temId],
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					const content = (
						<div>
							{text == null || text == "" ? null :
								<p><span style={{fontWeight: '700', color: '#333'}}>:</span> {text}</p>}
							{record.children == null || record.children == [] ? null : record.children.filter((item) => {
								return item.treeType === 'subtask'
							}).map((item, index) => {
								return (<p className={item.remark == null || item.remark == "" ? "hidden" : ""}
								           key={'task_' + index}><span
									style={{fontWeight: '700', color: '#333'}}>{index + 1}:</span> {item.remark}</p>)
							})}
						</div>
					);
					return (
						<div
							style={{width: '100%', height: '46px', lineHeight: '46px'}}
							onClick={this.onRow.bind(this, record)} id={record.id}>
							<Popover content={content} trigger="hover">
								<div
									style={{maxWidth: '180px'}}
									className="ellsis">
									{text == null || text == "" ? null :
										<span><span style={{fontWeight: '700', color: '#333'}}>:</span> {text}</span>}
									{record.children == null || record.children == [] ? null : record.children.filter((item) => {
										return item.treeType === 'subtask'
									}).map((item, index) => {
										return (<span key={'task_' + index}
										              className={item.remark == null || item.remark == "" ? "hidden" : ""}>
		                                  <span style={{paddingLeft: '5px', fontWeight: '700', color: '#333'}}>
		                                  {index + 1}:</span> {item.remark}</span>)
									})}
								</div>
							</Popover>
						</div>
					)
				}
			},
			{
				title: '',
				dataIndex: 'operation',
				align: "center",
				render: (text, record) => {
					let actionName = '';
					if (expandedKeys.filter(item => {
						return item == record.id
					}).length > 0 && !subTaskDis) {
						return (<span>
							       <a onClick={this.onCalac.bind(this, record.id)}></a>
								   <a className="ml15" onClick={this.saveSubTaskRecord.bind(this, record.id)}></a>
						       </span>)
					} else {
						// 
						let subTasks = record.children == null ? [] : record.children.filter((item) => {
							return item.treeType === 'subtask'
						})
						const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
						if (disabled) {  // 
							if (subNumber == 0) { // 0
								return <a onClick={this.handleApart.bind(this, record)}></a>
								//return <span>{subNumber}</span>
							} else {  // 0
								return (<span>
	                                <a title="" onClick={this.handleView.bind(this, record.id)}>{subNumber}</a>
	                                <Icon onClick={this.handleApart.bind(this, record)} className="pointer"
	                                      style={{marginLeft: "10px"}} title="" type="edit"/>
                                </span>)
							}
						} else {  // 
							return (
								<a onClick={this.handleApart.bind(this, record)}>{subNumber == 0 ? actionName : subNumber}</a>)
						}
					}
				}
			},
		]
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					personData: mobx.toJS(this.store.personData),
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
				}),
			};
		});
		const expandedRowRender = (record) => {
			// 
			let addRecord = {
				id: "",
				pid: record.id || "",
				projectId: record.projectId || "",
				treeType: "subtask",
				masters: record.masters == null ? [] : record.masters,
				alreadyHours: 0,
				totalHours: 0,
				rate: 0,
				name: "",
				remark: "",
				beginTime: null,
				endTime: null
			}
			return (
				<TaskSplite
					store={this.store}
					record={record}
					addRecord={addRecord}
					personData={mobx.toJS(this.store.personData)}
					dataSource={record.children == null ? [] : record.children.filter((item) => {
						return item.treeType === 'subtask'
					})}
					collapseOnChange={() => isChaijie && !disabled ? this.onPackup(record) : this.saveExpandedRows(record.id)}
					handleSaveSubTask={this.handleSaveSubTask}
					disabled={subTaskDis}
					id={record.id}
					onTaskRef={this.onTaskRef}
				/>
			)
		};
		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					expandedRowRender={expandedRowRender}
					rowKey={row => row.id}
					scroll={{ x: (columns.length-2)*140+140 }}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTable;




