import React from 'react';
import {inject, observer} from 'mobx-react';
import {Row, Col, Popover, Icon, Empty, Breadcrumb, Progress, Badge, Affix, Form, message, Modal, Button, Input, notification} from 'antd';

import * as  mobx from 'mobx';
import moment from "moment";

const { TextArea } = Input;

const openNotificationWithIcon = type => {
    notification[type]({
        message: '',
        description:
            '',
    });
};



@observer
export default class ProgressList extends React.Component {
    constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            visible: false,
            value: ""
        };
    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    // 
    scrollToAnchor(params) {
        const {id, key} = params;
        const {scrollToAnchor} = this.props;
        const {temId} = this.store;
        if (temId==undefined) {  // 
            scrollToAnchor(key);
        } else {
            scrollToAnchor(id);
        }
    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        if (this.state.value==""||this.state.value==null) {
            message.warn("")
            return false;
        }
        const params = {
            projectId: this.store.projectId,
            text: this.state.value
        }
        this.store.closeProject(params).then((res)=>{
            if (res) {
                openNotificationWithIcon("success");
                this.onCancel();
               // this.updateTree();
                this.store.getStageTree();
            } else {
                message.error("")
            }
        });
    };

    onCancel = () => {
        this.setState({
            visible: false,
            value: ""
        });
    }

    onChange = ({ target: { value } }) => {
        this.setState({ value });
    };

    render() {
        const {listData, isShow} = this.props;
        const {state, closeReason} = this.store;
        const content = (
            <div>
                <p>{state==1?"":closeReason}</p>
            </div>
        );

        return (
            <div>
                <Affix offsetTop={64}>
                   <div className="w clearfix"
                         style={{backgroundColor: '#FFF',
                             minHeight: '30px',
                             padding: '40px 30px'}}>
                        {listData.length>0?listData.map((item, index) => {
                            return (
                                <div
                                    onClick={this.scrollToAnchor.bind(this, item)}
                                    key={index}
                                    style={{
                                        width: '14%',
                                        float: 'left',
                                        display: 'inlineBlock',
                                        position: 'relative'
                                    }}>
                                    {item.rate>=100?
                                        <Badge className="worktile_progress_span_active" status="processing"/>:
                                        <span className="worktile_progress_span"/>}
                                   <Progress
                                        className="worktile_progress"
                                        style={{
                                            display: 'inlineBlock',
                                            paddingLeft: '9px'
                                        }}
                                        size="small"
                                        strokeLinecap="square"
                                        percent={item.rate==null?0:item.rate}/>
                                    <div style={{textAlign: 'left', position: 'relative', left: '-20px'}}>
                                        <p className={item.rate>0?"worktile_progress_item_active":"worktile_progress_item"}>{item.stageName}</p>
                                        <p className="worktile_progress_name">
                                            {item.masters==null?" ":item.masters.map(item=>item.userName).join("")}
                                            {item.rate==100?<Icon type="check-circle" style={{paddingLeft: '5px', fontSize: '16px', color: "#108DE9"}}/>:null}
                                        </p>
                                        <p className="worktile_progress_name">{item.finishTime==0?(item.rate>0?<a style={{cursor:"default"}}></a>:null):(item.rate==100?moment(item.finishTime).format("YYYY-MM-DD HH:mm"):<a style={{cursor:"default"}}></a>)}</p>
                                    </div>
                                </div>);
                        }):<Empty description={false} />}
                       <div
                           style={{
                               width: '2%',
                               float: 'left',
                               display: 'inlineBlock',
                               position: 'relative'
                           }}>
                           <span className="worktile_progress_span"/>
                           <div style={{textAlign: 'left', position: 'relative', left: '-22px', top: '22px'}}>
                               <p className={"worktile_progress_item"} style={{width: '80px'}}></p>
                           </div>
                       </div>
                       {!isShow?<Button
                               onClick={this.showModal}
                               className={state==0?"fr btn_yellow":"hidden"}
                               type="primary"
                               shape="round"
                               ghost>
                               
                           </Button>:null}
                       <Popover content={content}>
                           <Button className={state==1?"fr":"hidden"}
                                   type="primary"
                                   shape="round"></Button>
                       </Popover>
                       <Popover content={content}>
                           <Button className={state==2?"fr btn_yellow_active":"hidden"}
                                   type="primary"
                                   shape="round"></Button>
                       </Popover>
                    </div>
                </Affix>
                {/**/}
                <Modal
                    title=""
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.onCancel}
                >
                      <TextArea
                          value={this.state.value}
                          onChange={this.onChange}
                          placeholder=""
                          autoSize={{ minRows: 3, maxRows: 5 }}
                      />
                </Modal>
            </div>
        );
    }
}







