/*
* 
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Input, InputNumber, Form, Row, Col, Card, Button, Icon, Tooltip, message} from 'antd';
import ContentEditable from 'react-simple-contenteditable';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import PersonAdd2 from "@/components/worktile/PersonAdd";
import MyUpload from '@/components/worktile/MyUpload';
import {List} from 'immutable';

@Form.create()
@observer
export default class Ordercenter extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			fields: {
				"id": "",
				"pid": "",
				"orderRemark": "",
				"name": "",
				"masters": [],
				"customer": "",
				"customerPhone": "",
				"productType": ["5"],
				"address": "",
				"productNumber": "",
				"milepost": "",
				"priceProduct": 0,
				"priceDiscount": "",
				"priceDeal": 0,
				"notifications": [],
				"json": []
			},
			hidden: true,  // 
			keys: [],
			projectinfo: {}, // 
			orderinfo: {}, // 
		};
	}

	componentWillReceiveProps(nextProps) {
		const {itemObj} = nextProps;
		if (itemObj) {
			if (JSON.stringify(itemObj) !== JSON.stringify(this.props.itemObj)) {
				let projectinfo = itemObj.children.filter((item) => {
					return item.treeType == "projectinfo"
				})[0]; // 
				let orderinfo = projectinfo.children.filter((item) => {
					return item.treeType == "orderinfo"
				})[0];     // 

				let jsonString = orderinfo.json; //
				const josn = jsonString == null || (Array.isArray(jsonString) && jsonString.length == 0) ? [] : JSON.parse(jsonString);
				this.setState({
					projectinfo: Object.assign({}, projectinfo),
					orderinfo: Object.assign({}, orderinfo),
					fields: {
						"id": orderinfo.id,
						"pid": orderinfo.pid,
						"orderRemark": orderinfo.orderRemark == null ? "" : orderinfo.orderRemark,
						"name": projectinfo.name == null ? "" : projectinfo.name,
						"masters": projectinfo.masters == null ? [] : projectinfo.masters,
						"customer": projectinfo.customer == null ? "" : projectinfo.customer,
						"customerPhone": orderinfo.customerPhone,
						"productType": ["5"],
						"address": projectinfo.address == null ? "" : projectinfo.address,
						"productNumber": orderinfo.productNumber,
						"milepost": projectinfo.milepost == null ? "" : projectinfo.milepost,
						"priceProduct": orderinfo.priceProduct == null ? 0 : orderinfo.priceProduct,
						"priceDiscount": orderinfo.priceDiscount == null ? "" : orderinfo.priceDiscount,
						"priceDeal": orderinfo.priceDeal == null ? 0 : orderinfo.priceDeal,
						"notifications": orderinfo.masters == null ? [] : orderinfo.masters,
						"json": []
					},
					keys: Object.assign([], josn)
				});
			}
		}
	}

	componentDidMount() {
		const {itemObj, disabled} = this.props;
		let projectinfo = itemObj.children.filter((item) => {
			return item.treeType == "projectinfo"
		})[0]; // 
		let orderinfo = projectinfo.children.filter((item) => {
			return item.treeType == "orderinfo"
		})[0];     // 

		let jsonString = orderinfo.json; //
		const josn = jsonString == null || (Array.isArray(jsonString) && jsonString.length == 0) ? [] : JSON.parse(jsonString);
		this.setState({
			projectinfo: Object.assign({}, projectinfo),
			orderinfo: Object.assign({}, orderinfo),
			fields: {
				"id": orderinfo.id,
				"pid": orderinfo.pid,
				"orderRemark": orderinfo.orderRemark == null ? "" : orderinfo.orderRemark,
				"name": projectinfo.name == null ? "" : projectinfo.name,
				"masters": projectinfo.masters == null ? [] : projectinfo.masters,
				"customer": projectinfo.customer == null ? "" : projectinfo.customer,
				"customerPhone": orderinfo.customerPhone,
				"productType": ["5"],
				"address": projectinfo.address == null ? "" : projectinfo.address,
				"productNumber": orderinfo.productNumber,
				"milepost": projectinfo.milepost == null ? "" : projectinfo.milepost,
				"priceProduct": orderinfo.priceProduct == null ? 0 : orderinfo.priceProduct,
				"priceDiscount": orderinfo.priceDiscount == null ? "" : orderinfo.priceDiscount,
				"priceDeal": orderinfo.priceDeal == null ? 0 : orderinfo.priceDeal,
				"notifications": orderinfo.masters == null ? [] : orderinfo.masters,
				"json": []
			},
			keys: Object.assign([], josn),
			hidden: disabled
		});
		this.props.onRef(this);
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {projectinfo, orderinfo, fields, keys} = _this.state;
		if (keys.filter((item)=>{ return item.key==""||item.key==null}).length>0) {
             message.warn("")
			return false;
		}
		const {projectId} = _this.store;
		_this.props.form.validateFields((err, values) => {
			if (!err) {
				let project = JSON.parse(JSON.stringify(projectinfo));
				let order = JSON.parse(JSON.stringify(orderinfo));
				delete project.children;
				if (order.children == null) {
					delete order.children;
				}
				const params = {
					project: Object.assign(project, {
						"projectId": projectId,
						"name": values.name,
						"masters": fields.masters,
						"customer": values.customer,
						"address": values.address,
						"milepost": values.milepost,
					}),
					orderinfo: Object.assign(order, {
						"projectId": projectId,
						"orderRemark": values.orderRemark,
						"customerPhone": values.customerPhone,
						"productNumber": values.productNumber,
						"masters": fields.notifications,
						"json": JSON.stringify(keys),
						"priceProduct": values.priceProduct == "" || values.priceProduct == null ? 0 : Number(values.priceProduct),
						"priceDiscount": values.priceDiscount == "" || values.priceDiscount == null ? "" : values.priceDiscount,
						"priceDeal": values.priceDeal == "" || values.priceDeal == null ? 0 : Number(values.priceDeal)
					})
				}
				_this.props.saveProject(params);
			}
		});
	}

	remove = k => {
		const keys = JSON.parse(JSON.stringify(this.state.keys));
		keys.splice(k, 1);
		this.setState({
			keys
		});
	};

	// 
	add = () => {
		const keys = JSON.parse(JSON.stringify(this.state.keys));
		keys.push({key: "", value: ""})
		this.setState({
			keys
		});
	};

	handleChange(index, e, val) {
		let customList = JSON.parse(JSON.stringify(this.state.keys));
		customList[index].key = val;
		this.setState({
			keys: Object.assign([], customList)
		});
	}

	changeNew = (index, e) => {
		const {value} = e.target;
		let customList = JSON.parse(JSON.stringify(this.state.keys));
		let list = List(customList);
		list.update(index, (val) => {
			val.value = value;
			return val;
		});
		this.setState({
			keys: Object.assign([], list.toJS())
		});
	}

	// 
	updatePerson = (type, userIds) => {
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {
				[type]: userIds
			})
		});
	}

	// 
	onChangeVis = () => {
		this.setState({
			hidden: !this.state.hidden,
		});
		this.props.updateCount('count', Number(this.props.count) + 1)
	}

	// 
	onCanlc = () => {
		this.props.form.resetFields();
		this.setState({
			hidden: !this.state.hidden
		});
	}

	updateTip = (type) => {
		const val = this.props.form.getFieldValue('priceProduct')
		if (type=="notifications"&&(val==""||val==null)) {  // 
			//message.warn("");
			return false;
		} else {
			return true;
		}
	}

	render() {
		const {fields, hidden, keys,} = this.state;
		const {projectId, state} = this.store;
		const {count, itemObj, ossData, saveAttachment, delNode} = this.props;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 10},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 14},
			},
		};
		const formItems = keys.map((k, index) => (
			<Col xs={24} sm={24} md={18} lg={12} xl={8} key={'keys' + index}>
				<Form.Item
					{...formItemLayout}
					label={
						hidden ? (k.key == null ? "" : k.key) : <ContentEditable
							html={k.key == null ? "" : k.key}
							className="edite_div bd1"
							tagName="h1"
							onKeyPress={this.handleChange.bind(this, index)}
							onChange={this.handleChange.bind(this, index)}
							contentEditable="plaintext-only"
							disabled={hidden}
						/>}
					className='w pr'
				>
					{getFieldDecorator('key' + index, {
						//initialValue: k.value,
						rules: [
							{
								required: false,
								message: "",
							}
						],
					})(
						<div className={hidden ? "w dib" : "w90 dib"}>
							<Input
								placeholder=""
								value={k.value == null ? "" : k.value}
								onChange={this.changeNew.bind(this, index)}
								disabled={hidden}
							/>
						</div>
					)}
					<Tooltip title="">
						<Icon
							className={hidden ? "hidden" : ""}
							style={{marginLeft: '5px'}}
							type="minus-circle-o"
							onClick={() => this.remove(index)}
						/>
					</Tooltip>
				</Form.Item>
			</Col>
		));
		let isShow = projectId == null || projectId == "" ? true : false;
		let attcObj = itemObj.children != null && itemObj.children.filter((item) => {
			return item.treeType === 'attc'
		})[0];  //
		const attachments = attcObj.children != null ? attcObj.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		return (
			<Card bordered className="pr" style={{width: '100%'}}>
				<h3 id={itemObj.id||"items0"} className="kanban_title mb20"></h3>
				<span className="worktile_progress_add">
                     {hidden ?
	                     <Button className={state == 2 ? "hidden" : ""} type="link"
	                             onClick={this.onChangeVis}>{count == 1 ? "" : ""}</Button> :
	                     <div>
		                     <span onClick={this.onCanlc} className="mr10"></span>
		                     <span onClick={this.handleSubmit}></span>
	                     </div>}
                </span>
				<Form className={count == 1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('name', {
									initialValue: fields.name || "",
									rules: [{required: true, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('masters', {
									initialValue: fields.masters == null ? [] : fields.masters,
									rules: [{required: false, message: ''}],
								})(
									<PersonAddIcon
										data={fields.masters == null ? [] : fields.masters}
										updatePerson={this.updatePerson.bind(this, 'masters')}
										disabled={hidden}
									/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('address', {
									initialValue: fields.address || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('customer', {
									initialValue: fields.customer || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('customerPhone', {
									initialValue: fields.customerPhone || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('milepost', {
									initialValue: fields.milepost || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('productType', {
									initialValue: fields.productType || [],
									rules: [{required: false, message: ''}],
								})(
									<span></span>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('productNumber', {
									initialValue: fields.productNumber || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('orderRemark', {
									initialValue: fields.orderRemark || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="()" className="w">
								{getFieldDecorator('priceProduct', {
									initialValue: fields.priceProduct || "",
									rules: [{required: false, message: ''}],
								})(
									<InputNumber placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('priceDiscount', {
									initialValue: fields.priceDiscount || "",
									rules: [{required: false, message: ''}],
								})(
									<Input placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="()" className="w">
								{getFieldDecorator('priceDeal', {
									initialValue: fields.priceDeal || "",
									rules: [{required: false, message: ''}],
								})(
									<InputNumber placeholder="" className="w" disabled={hidden}/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('notifications', {
									initialValue: fields.notifications == null ? [] : fields.notifications,
									rules: [{required: false, message: ''}],
								})(
									<PersonAdd2
										data={fields.notifications == null ? [] : fields.notifications}
										updatePerson={this.updatePerson.bind(this, 'notifications')}
										disabled={hidden}
										updateTip={this.updateTip.bind(this, 'notifications')}
									/>
								)}
							</Form.Item>
						</Col>
						{formItems}
					</Row>
				</Form>
				<Row className={count == 1 ? "hidden" : ""}>
					<Col>
						<Tooltip title="">
							<Icon onClick={this.add}
							      className={hidden ? "hidden" : ""}
							      style={{margin: '0 20px', color: '#4BA4BE', fontSize: '22px'}} type="plus-circle"/>
						</Tooltip>
						{isShow ? null :
							<div className={hidden ? "mt15" : "myupload_pos"} style={{top: '-27px', left: '53px'}}>
								<MyUpload
									saveAttachment={saveAttachment}
									delNode={delNode}
									fileList={attachments}
									disabled={hidden}
									pid={attcObj.id}
									projectId={attcObj.projectId}
									ossData={ossData}
								/>
							</div>}
					</Col>
				</Row>
			</Card>
		)
	}
}



