import React from 'react';
import {inject, observer} from 'mobx-react';
import {Card, Form, Row, Col, Button, message} from 'antd';
import * as  mobx from 'mobx';

import EditableTable from "@/components/worktile/EditableTable";
import MyUpload from "@/components/worktile/MyUpload";
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as utils from "@/utils/utils";

@Form.create()
@observer
class WortileCommon extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],   // 
			userIds:[],
            hidden: true,  // 
            currentTree: {}, // 
            pid: '',
            projectId: "",
            defaultTree: {}, // 
        };
	}

	componentWillReceiveProps(nextProps) {
        /*
        *    taskc nextProps.data["children"][0]["children"]
        * */
        if (nextProps.treeData!==this.props.treeData) {
            /**/
            if (nextProps.treeData.children===null) { // 
                this.setState({
                    dataSource: []
                });
            } else {  // 
                const dataSource = nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].children;
                this.setState({
                    dataSource,
                    currentTree: Object.assign({}, nextProps.treeData),   // 
                    pid: nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].id,
                    projectId: nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].projectId,
                    hidden: nextProps.disabled,
                }, ()=>{
                    // 
                    let attc = nextProps.treeData.children.filter((item)=>item.treeType=="attc")[0]
                    if (Object.keys(this.state.defaultTree).length>0) {
                        // tree
                        let defaultTree = JSON.parse(JSON.stringify(this.state.defaultTree));
                        this.setState({
                            defaultTree: utils.updateTreeData(defaultTree, "update", attc)
                        });
                    }
                })
            }
        }
	}

	componentDidMount() {
        /*
        *    taskc nextProps.data["children"][0]["children"]
        * */
        if (this.props.treeData) {
            /**/
            if (this.props.treeData.children===null) { // 
                this.setState({
                    dataSource: []
                });
            } else {  // 
                const dataSource = this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].children;
                this.setState({
                    dataSource,
                    currentTree: Object.assign({}, this.props.treeData),
                    pid: this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].id,
                    projectId: this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].projectId,
                    hidden: this.props.disabled
                })
            }
        }
	}

	handleSelChange = (val, e) => {
		let userIds;
		if (e.length) {
			userIds = e.map((item)=>{return {userId: item.props.value, userName: item.props.children}})
		} else {
			userIds  = []
		}
		this.setState({
			userIds
		});
		const {currentTree} = this.state;
		// 
        const currentData = Object.assign({}, currentTree, {masters:userIds});
        this.setState({
            currentTree: Object.assign({}, currentTree, currentData)
        });
    }

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {currentTree, defaultTree} = _this.state;
		// 1 
		let currentDataSource = currentTree.children!=null&&currentTree.children[0].children!=null?currentTree.children[0].children:[];
		const isSub = currentDataSource.filter((item)=>{return item.name==""||item.name==null})
		if (isSub.length>0) {  // true
			message.warn("");
			return false;
		}
		const isEndime = currentDataSource.filter((item)=>{return item.beginTime!=""&&item.beginTime!=null&&(item.endTime==""||item.endTime==null)})
		if (isEndime.length>0) {  // true
			message.warn("");
			return false;
		}
		// 2
        // 
        let defaultSource=defaultTree.children!=null&&defaultTree.children[0].children!=null?defaultTree.children[0].children.map(item=>{
            return {
                "name": item.name,
                "masters": item.masters,
                "beginTime": item.beginTime,
                "endTime": item.endTime,
                "rate": item.rate
            }
        }):[];
        let currentSource = currentTree.children!=null&&currentTree.children[0].children!=null?currentTree.children[0].children.map(item=>{
            return {
                "name": item.name,
                "masters": item.masters,
	            "beginTime": item.beginTime,
	            "endTime": item.endTime,
                "rate": item.rate
            }
        }):[];
        const isChange = JSON.stringify(defaultSource)==JSON.stringify(currentSource); // 
        const isMasters = currentTree&&currentTree.masters==null?[]:currentTree.masters; //
        if (isMasters.length==0&&isChange==false) {
            message.warn("");
            return false;
        }
        _this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                _this.props.saveNodes(currentTree);
                let comStates = JSON.parse(JSON.stringify(_this.store.comStates));
                _this.store.comStates = Object.assign(comStates, {[_this.props.id]: true})
            }
        });
	}

    //  
    saveData2 = (taskdata, key) => {
        const {currentTree} = this.state;
        let newData = [];
        taskdata.forEach(item=>{
            let {key, children, ...obj} = item;
            newData.push(obj);
        });
        let currentTaskC = currentTree.children.filter(item=> item.treeType==='taskc')[0];
        currentTaskC = Object.assign(currentTaskC,{"children":taskdata})
        let stepChildren = currentTree.children;
        const taskcIndex = stepChildren.findIndex(item=> {return item.treeType==='taskc'} )
        stepChildren[taskcIndex] = currentTaskC;
        this.setState({currentTree:Object.assign(currentTree, {"children":stepChildren})});
    }

    // 
    onChangeVis = () => {
	    let comStates = JSON.parse(JSON.stringify(this.store.comStates));
        this.store.comStates = Object.assign(comStates, {[this.props.id]: false});
	    let comCount = JSON.parse(JSON.stringify(this.store.comCount));
	    this.store.comCount = Object.assign(comCount, {[this.props.id]: Number(this.props.count)+1});
        // 
        this.setState({
            defaultTree: Object.assign({}, this.state.currentTree)
        });
    }

    // 
    onCanlc = ()=> {
        this.props.form.resetFields();
        this.onChangeVis();
        let comStates = JSON.parse(JSON.stringify(this.store.comStates));
        this.store.comStates = Object.assign(comStates, {[this.props.id]: true})
        // tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
    }

    // 
    updatePerson = (userIds)=>{
        const {currentTree} = this.state;
	    // 
        const currentData = Object.assign({}, currentTree, {masters:userIds});
        // tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
    }

	render() {
		let _this = this;
		const {getFieldDecorator} = _this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const { hidden, currentTree } = _this.state;
		const {state} = _this.store;
		const {isShow, count}=this.props;
		let currentDataSource = currentTree.children!=null&&currentTree.children[0].children!=null?currentTree.children[0].children:[];
        let attcObj = currentTree.children!=null&&currentTree.children.filter((item)=>{return item.treeType==='attc'})[0];  //
        const attachments = attcObj.children!=null?attcObj.children.map((vitem) => {
            return Object.assign({}, {
                uid: vitem.id,
                name: vitem.fileName,
                status: 'done',
                url: vitem.filePath,
                fileId: vitem.fileId,
                fileName:vitem.fileName,
                filePath: vitem.filePath,
                id: vitem.id
            })
        }) : [];
        let expandedKeys = mobx.toJS(_this.store.expandedKeys)[this.props.id]!== undefined?mobx.toJS(_this.store.expandedKeys)[this.props.id]:[]
        let isExp = expandedKeys.length>0?true:false;
        return (
			<Card bordered style={{width: '100%'}} className="mt20">
				<h3 id={this.props.id} className="kanban_title mb20">{_this.props.treeData.stageName}</h3>
                <span className="worktile_progress_add">
                    {hidden ?
	                    <Button className={state == 2 ? "hidden" : ""}
	                            disabled={isShow}
	                            type="link"
	                            onClick={this.onChangeVis}>{count==1? "" : ""}</Button> :
	                    <div>
		                    <span onClick={this.onCanlc} className="mr10"></span>
		                    <Button disabled={isExp} type="link" onClick={this.handleSubmit}></Button>
	                    </div>}
                </span>
                <Form className={count==1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
                            <Form.Item label="" className="w">
                                {getFieldDecorator('masters', {
                                    initialValue: currentTree&&currentTree.masters==null?[]:currentTree.masters.map(item=>item.userId),
                                    rules: [{required: false, message: ''}],
                                })(
                                    <PersonAddIcon
                                        data={currentTree&&currentTree.masters==null?[]:currentTree.masters}
                                        updatePerson={this.updatePerson}
                                        disabled={hidden}
                                    />
                                )}
                            </Form.Item>
						</Col>
					</Row>
				</Form>
                {/**/}
				{count==1?null:
                <EditableTable
                    id={this.props.id}
                    dataSource={currentDataSource}
                    saveData={this.saveData2}
                    projectId={this.state.projectId}
                    pid={this.state.pid}
                    disabled={hidden}
                    store={this.store}
                />}
				{count==1?null:
                <div className={hidden?"mt15":"myupload_pos"}>
                    <MyUpload
                        saveAttachment={this.props.saveAttachment}
                        delNode={this.props.delNode}
                        fileList={attachments}
                        pid={attcObj.id}
                        projectId={attcObj.projectId}
                        disabled={hidden}
                        ossData={this.props.oss}
                    />
                </div>}
			</Card>
		);
	}
}


export default WortileCommon;



